import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Typography from '@material-ui/core/Typography';
import CardActionArea from '@material-ui/core/CardActionArea';
import Box from '@material-ui/core/Box';

const useStyles = makeStyles({
    root: {
      maxWidth: 245,
      height: 380,
      textAlign: 'center',
    },
    media: {
      height: 140,
    },
    activityName: {
      color: "#2C689D",
    },
    summary: {
      color: "#757575",
    }
  });

const Activity = ({ activity, displayDetails }) => {
    const classes = useStyles();

    return (
    <CardActionArea onClick={displayDetails}>
      <Typography component="div">
        <Card className={classes.root}>
            <CardContent>
              <CardMedia
                className={classes.media}
                image={activity.image}
                title="Activity image"
              />
              <Box className={classes.activityName} m={1}>
                {activity.name}
              </Box>
              <Box className={classes.summary}>
                {activity.summary}
              </Box>
              <Box className={classes.summary} fontStyle="italic" mt={3}>
                Completed: {activity.completed_steps}/{activity.num_steps}
              </Box>
            </CardContent>
        </Card> 
      </Typography>
    </CardActionArea>
    )
}

export default Activity