import React, { useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import ChannelDescription from './ChannelDescription';
import Activity from './Activity';
import ActivityDetails from './ActivityDetails';
import SectionDivider from './misc/SectionDivider';
import Grid from '@material-ui/core/Grid';

const useStyles = makeStyles({
    root: {
      maxWidth: 245,
      height: 400,
    },
    list: {
      flexGrow: 1,
      padding: 40,
    },
    media: {
      height: 140,
    },
    center: {
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'center',
    },
  });

const ChannelDetails = ({ channel }) => {
    const [activityView, setActivityView] = useState(0) 
    const classes = useStyles();

    const displayDetailsOf = (id) => {
        setActivityView(id)
      }

    if (activityView === 0) {
        return (
            <Grid container className={classes.center}>
                <ChannelDescription image={channel.image} description={channel.description} name={channel.name}/>
                <Grid item xs={12}>       
                         <SectionDivider text="Related activities"/>
                         <Grid container justify="center" spacing={1}>
                            {channel.activities.map((activity, i) => (
                            <Grid item lg={2} md={3} xs={6}>
                                <Activity
                                key={i}
                                activity={activity}
                                displayDetails={() => displayDetailsOf(activity.id)}
                                />
                            </Grid>
                            ))}
                          </Grid>
                </Grid>
            </Grid>
        )
    } else {
        return (
            <ActivityDetails activity={channel.activities[activityView-1]}/>
        )
    }
}

export default ChannelDetails